import React, { Fragment } from 'react'
import { Menu as MenuSUI, Container } from 'semantic-ui-react'
import { Link } from 'react-router-dom'
import scrollTo from '../../helpers/scrollTo'

class Menu extends React.Component {
  state = { activeItem: 'Inicio' }

  handleItemClick = (e, { name }) => {
    this.setState({ activeItem: name })
    scrollTo(`#${name}`)
  }

  render() {
    const { activeItem } = this.state
    const { page } = this.props

    return (
      <Container>
        <div className="ui secondary top large pointing menu border-0">
          <div className="left item">
            <Link className="toc item" to="#" onClick={this.props.onShowClick}>
              <i className="sidebar icon"></i>
            </Link>
          </div>

          <div className="right item">
            <MenuSUI.Item
              as={Link}
              to="/"
              name="Inicio"
              active={activeItem === 'Inicio'}
              onClick={this.handleItemClick}
            />
            {page === '/' && (
              <Fragment>
                <MenuSUI.Item
                  name="Acerca"
                  active={activeItem === 'Acerca'}
                  onClick={this.handleItemClick}
                />
                <MenuSUI.Item
                  name="Habilidades"
                  active={activeItem === 'Habilidades'}
                  onClick={this.handleItemClick}
                />
                <MenuSUI.Item
                  name="Portafolio"
                  active={activeItem === 'Portafolio'}
                  onClick={this.handleItemClick}
                />
              </Fragment>
            )}
            <MenuSUI.Item
              name="Contacto"
              active={activeItem === 'Contacto'}
              onClick={this.handleItemClick}
            />
            <MenuSUI.Item
              as={Link}
              to="/blog"
              name="Blog"
              active={activeItem === 'Blog'}
              onClick={this.handleItemClick}
            />
          </div>
        </div>
      </Container>
    )
  }
}

export default Menu
