import React, { Fragment } from 'react'
import { Route, Link } from 'react-router-dom'
import {
  Menu as MenuSUI,
  Segment,
  Sidebar as SidebarSUI
} from 'semantic-ui-react'
import scrollTo from '../../helpers/scrollTo'
import Menu from './Menu'

class Layout extends React.Component {
  state = { visible: false, page: '/' }

  componentWillReceiveProps(nextProps, nextState) {
    this.setState({ page: nextProps.path })
  }

  handleShowClick = () => this.setState({ visible: true })
  handleSidebarHide = () => this.setState({ visible: false })
  handleItemClick = (e, { name }) => {
    this.setState({ visible: false })
    scrollTo(`#${name}`)
  }

  render() {
    const { visible, page } = this.state

    return (
      <Fragment>
        {/* Sidebar */}
        <SidebarSUI.Pushable as={Segment}>
          <SidebarSUI
            as={MenuSUI}
            animation="push"
            icon="labeled"
            onHide={this.handleSidebarHide}
            vertical
            visible={visible}
            width="wide"
          >
            <MenuSUI.Item
              as={Link}
              to="/"
              name="Inicio"
              onClick={this.handleItemClick}
            >
              Inicio
            </MenuSUI.Item>
            {page === '/' && (
              <Fragment>
                <MenuSUI.Item
                  as="a"
                  name="Acerca"
                  onClick={this.handleItemClick}
                >
                  Acerca
                </MenuSUI.Item>
                <MenuSUI.Item
                  as="a"
                  name="Habilidades"
                  onClick={this.handleItemClick}
                >
                  Habilidades
                </MenuSUI.Item>
                <MenuSUI.Item
                  as="a"
                  name="Portafolio"
                  onClick={this.handleItemClick}
                >
                  Portafolio
                </MenuSUI.Item>
              </Fragment>
            )}
            <MenuSUI.Item as="a" name="Contacto" onClick={this.handleItemClick}>
              Contacto
            </MenuSUI.Item>
            <MenuSUI.Item as={Link} name="Blog" to="/blog">
              Blog
            </MenuSUI.Item>
          </SidebarSUI>

          <SidebarSUI.Pusher dimmed={visible}>
            {/* Menu */}
            <Menu
              visible={visible}
              onShowClick={this.handleShowClick}
              page={page}
            />

            {/* Content */}
            <main>
              <Route {...this.props} />
            </main>
          </SidebarSUI.Pusher>
        </SidebarSUI.Pushable>
      </Fragment>
    )
  }
}

export default Layout
