import React, { Fragment } from 'react'
import ReactMarkdown from 'react-markdown'
import Contact from '../../../../Contact'

class Post1 extends React.Component {
  state = {
    markdown: 'Cargando...'
  }

  componentDidMount() {
    const pa = require('./autenticacion-con-laravel-react-y-passport-parte-1.md')
    fetch(pa)
      .then(response => response.text())
      .then(response => {
        this.setState({
          markdown: response
        })
      })
  }

  render() {
    return (
      <Fragment>
        <section
          className="ui segment padded aligned py-3"
          style={{ textAlign: 'justify' }}
        >
          <img
            className="ui centered big image pb-3"
            src={process.env.PUBLIC_URL + '/images/posts/2019/07/20/post1.png'}
            alt="Autenticación con Laravel, React y Passport - Parte 1"
          />

          <div className="ui text container pb-3">
            <h2 className="ui header header-text centered pb-1">
              Autenticación con Laravel, React y Passport - Parte 1
            </h2>

            <ReactMarkdown source={this.state.markdown} />

            <img
              className="ui centered big image"
              src={
                process.env.PUBLIC_URL + '/images/posts/2019/07/20/headers.png'
              }
              alt="Headers"
            />

            <h4 className="ui header header-text pb-1">
              7.1 Registro de usuarios
            </h4>
            <img
              className="ui centered big image"
              src={
                process.env.PUBLIC_URL + '/images/posts/2019/07/20/register.png'
              }
              alt="Register"
            />

            <h4 className="ui header header-text pb-1">7.2 Inicio de sesión</h4>
            <img
              className="ui centered big image"
              src={
                process.env.PUBLIC_URL + '/images/posts/2019/07/20/login.png'
              }
              alt="Login"
            />

            <h4 className="ui header header-text pb-1">
              7.3 Visualización de usuario
            </h4>
            <img
              className="ui centered big image"
              src={process.env.PUBLIC_URL + '/images/posts/2019/07/20/user.png'}
              alt="User"
            />

            <h4 className="ui header header-text pb-1">7.3 Cerrar sesión</h4>
            <img
              className="ui centered big image"
              src={
                process.env.PUBLIC_URL + '/images/posts/2019/07/20/logout.png'
              }
              alt="Logout"
            />

            <p className="pt-3">
              Ahora que ya hemos probado nuestros endpoints, damos por concluida
              esta primera parte.
            </p>
          </div>
        </section>

        <div id="Contacto">
          <Contact />
        </div>
      </Fragment>
    )
  }
}

export default Post1
