import React from 'react'
import { HashRouter as Router, Switch } from 'react-router-dom'

import App from './App'
import Blog from './Blog'
import Layout from './partials/Layout'

import Post1 from './posts/2019/07/20/autenticacion-con-laravel-react-y-passport-parte-1'

const Routes = () => (
  <Router>
    <Switch>
      <Layout path="/" exact component={App} />
      <Layout path="/blog" exact component={Blog} />

      {/* Posts */}
      <Layout
        path="/blog/2019/07/20/autenticacion-con-laravel-react-y-passport-parte-1"
        exact
        component={Post1}
      />
    </Switch>
  </Router>
)

export default Routes
